import React, { lazy, Suspense } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Loading from "components/shared-components/Loading";
import { APP_PREFIX_PATH } from "configs/AppConfig";

export const AppViews = () => {
  return (
    <Suspense fallback={<Loading cover="content" />}>
      <Switch>
        <Route
          path={`${APP_PREFIX_PATH}/home`}
          component={lazy(() => import(`./home`))}
        />
        <Route
          path={`${APP_PREFIX_PATH}/reservations`}
          component={lazy(() => import(`./reservations`))}
        />
        <Route
          path={`${APP_PREFIX_PATH}/products`}
          component={lazy(() => import(`./products`))}
        />
        <Route
          path={`${APP_PREFIX_PATH}/orders`}
          component={lazy(() => import(`./orders`))}
        />
        <Route
          path={`${APP_PREFIX_PATH}/profile`}
          component={lazy(() => import(`./profile`))}
        />
        <Route
          path={`${APP_PREFIX_PATH}/create-excel`}
          component={lazy(() => import(`./createExcel`))}
        />
        <Route
          path={`${APP_PREFIX_PATH}/change-password`}
          component={lazy(() => import(`./changePassword`))}
        />
        <Route
          path={`${APP_PREFIX_PATH}/add-product`}
          component={lazy(() => import(`./addProduct`))}
        />
        <Route
          path={`${APP_PREFIX_PATH}/product-details`}
          component={lazy(() => import(`./products/productDetails`))}
        />

        <Redirect from={`${APP_PREFIX_PATH}`} to={`${APP_PREFIX_PATH}/home`} />
      </Switch>
    </Suspense>
  );
};

export default React.memo(AppViews);
